		//content
		#container{}
		#content{}
		.content-wrap{
			&__outer{}
			&__inner{}
			.withParents-time-spent{
				&__area{}
				&__box{
					.tab{
						&__items{height: 191px;padding-top: 105px;display: flex;justify-content: center;box-sizing: border-box}
						&__item{height: 79px;background: no-repeat 0 0;
							&:nth-last-of-type(1){margin-right: 0 !important;}
							&.default{width: 216px;background-image: url("/assets/images/with_parents_time_spent/bg_tab01_default.png");margin-right: 30px;}
							&.active{width: 224px;background-image: url("/assets/images/with_parents_time_spent/bg_tab01_active.png");margin-right: 22px;
								.btn{color: #5d9eed;}
							}
							.btn{width: 216px;height: 60px;
								&-tab{@include font($gro, $font-r, 28px, 64px, #fff);letter-spacing: 2px;}
							}
						}
					}
					.chart{position: relative;height: 508px;
						&__v-axis{position: absolute;top: 28px;left: 50px;
							&__items{display: flex;flex-flow: column-reverse;justify-content: space-between;height: 428px;}
							&__item{width: 46px;height: 42px;background: url("/assets/images/with_parents_time_spent/icon_star03.png") no-repeat 0 0;}
							.num{@include font($nsroundecp, $font-b, 18px, 46px, #5d9eed);}
						}
						&__h-axis{position: absolute;bottom: 4px;left: 156px;
							&__items{display: flex;flex-flow: row;justify-content: space-between;width: 757px;}
							&__item{width: 70px;
								&.sat,
								&.sun{
									.on-week{color: #f78faf;}
									.date-box{color: #f8acc3;}
								}
							}
							.on-week{@include font($notoecp, $font-r, 20px, 1em, #5d9eed);
								&:first-letter{text-transform: uppercase}
							}
							.date-box{display: flex;justify-content: center; margin-top: 5px;@include font($notoecp, $font-r, 15px, 1em, #8dbbf3);}
							.date{}
							.separator{padding: 0 5px;}
							.month{
								&:first-letter{text-transform: uppercase}
							}
							.num{}
						}
						&__label{position: absolute;top: -20px;right: 20px;
							.txt{@include font($nsroundecp, $font-b, 13px, 1em, #5d9eed);}
						}
						&__graph{position: relative;margin: 44px 0 0 102px;width: 848px;height: 444px;background: url("/assets/images/with_parents_time_spent/bg_chart_map01.png") no-repeat 0 0;
							&__items{overflow: hidden;position: absolute;bottom: 6px;left: 50px;display: flex;flex-flow: row;justify-content: space-between;align-items: flex-end;width: 757px;height: 467px;}
							&__item{width: 70px;display: flex;align-items: flex-end;justify-content: center;height: 388px;
								&.sat,
								&.sun{
									.graph-txt{color: #f78faf;}
									.graph-box{background-color: #f78faf;}
									.icon{
										&-star{background-image: url("/assets/images/with_parents_time_spent/icon_star02.png");}
									}
								}
								.graph{
									&-box{position: relative;top: 25px;width: 56px;background-color: #90c1f4;}
									&-txt{position: absolute;top: -44px;left: 0;align-self:flex-start;width: 100%;@include font($notoecp, $font-r, 17px, 1em, #5d9eed);}
									&-bar{max-height:436px;height: 0;transition: height 1s $ease-out-cubic;
										.no-data{color: #9e9c9d;}
									}
								}
							}
							.icon{
								&-star{position: absolute;top: -25px;left: -7px;width: 70px;height: 63px;background: url("/assets/images/with_parents_time_spent/icon_star01.png") no-repeat 0 0;}
							}
						}
					}
				}
			}
			.btn{display: block;width: 100%;height: 100%;}
		}
	//}
